import React, { useContext } from "react";
import { useTranslation } from 'react-i18next';
import { StoreContext } from '../../../store';
import {Row,Col} from 'antd'

const RowDataModal = ({
    textLeft,           // Ni dung Bn tri
    textRight,          // Ni dung bn phi
    textRight2,         // Ni dung ph bn phi
    rightComponent,     // Commponent bn phi nu c (S dng input, ...)
    dataSub = [],       // Ni dung sub
    rightColor,         // Color bn phi
    type = 'modal',     // type ca Row 
    last,               // c phi l button cui cng ca list button hay khng
    ...props
}) => {
    const { styles } = useContext(StoreContext);
    const { t } = useTranslation()
    return (
        <Row className="align-items-center" style={{
            borderBottom: last ? "0" : '1px solid var(--DIVIDER__MODAL__COLOR)'
        }}>
            <Col span={13} className="text-left">
                <div style={{ color: styles.SECOND__CONTENT__COLOR}}>{t(textLeft)}</div>
            </Col>
            <Col span={11} className="text-left">
                {dataSub[0] && <div className="flex align-items-center" >
                    <div className="mr-1 text-uppercase" style={{color: styles.PRIMARY__CONTENT__COLOR }}>
                        {dataSub[0]}
                    </div>
                    <div  style={{backgroundColor: styles.PRIMARY,borderRadius:'5px' }}>
                        <div className="fs-13 fw-bold text-white" style={{padding:'2px' }}>{dataSub[1]}</div>
                    </div>
                </div>}
                {textRight && <div style={{ color: rightColor ? rightColor : styles.PRIMARY__CONTENT__COLOR}}>
                    {t(textRight)}
                </div>}
                {textRight2 && <div style={{ color: styles.SECOND__CONTENT__COLOR}}>
                    {t(textRight2)}
                </div>}
                {rightComponent}
            </Col>
        </Row>
    )
}

export default RowDataModal
