

/**
 * 
 * @param {Number} timeout Thi gian ch trc khi thc hin cng vic tip theo
 */

const wait = (timeout) => {
    return new Promise(resolve => setTimeout(resolve, timeout));
}


/**
 * 
 * @param {Function} func Hm cn x l m khng gy nh hng n UI, animation
 * @param {Number} timeout Thi gian (ms) waiting trc khi gi cc hm (optional)
 * 
 * @returns {smoothJSProcessing} Cc hm JS c thc thi bt ng b bn trong hm smoothJSProcessing mt cc mt m, khng nh hng ti cc UI, process khc
 */

const smoothJSProcessing = (func, timeout = 10) => {
    const Processing = new Promise(resolve => setTimeout(resolve, timeout));
    Processing.then(() => func())
}

export {
    wait,
    smoothJSProcessing
}