import { glb_sv, socket_sv, control_sv, requestInfo, InputServiceBody } from '../index'
import isEqual from 'lodash/isEqual'

/**
 * @param {Object} serviceInfo                  Thng tin c bn ca service
 * @param {Array} inputParams                   Mng cc tham s ca service cn ly
 * @param {Function} handleResultFunc           Hm x l kt qu
 * @param {Boolean} isControlTimeOut            Xc nh c x l TimeOut hay khng. Mc nh lun c timeOut (isControlTimeOut = true)
 * @param {Function} onTimeout       log, warn, openmodal hay x l li cho tng request c bit (optional)
 * @param {String} OTP                          Truyn OTP cho request login
 * @param {Number} time                         Thi gian time out cho request. Mc nh ly t timeout config
 * mc nh ch log "Request b timeOut"
 * 
 */
const sendRequest = (serviceInfo, inputParams, handleResultFunc, isControlTimeOut = true, onTimeout = () => null, OTP, time, isClearReqInfoMap) => {
    if (!socket_sv.socket_trading?.connected) {
        console.warn("Mng khng n nh, vui lng th li sau: [ServiceInfo]:", serviceInfo, ' [inputParams]: ', inputParams);
        // Nu request c hm time out th cn phi x l ==> d cc state tr v trng thi ban u
        onTimeout({ type: 'not_network', inputParams })
        return
    }

    // Nu khng c th gi request
    const clientSeq = serviceInfo.clientSeq || socket_sv.getRqSeq();
    const inputServiceBody = new InputServiceBody(
        clientSeq,
        serviceInfo.WorkerName,
        serviceInfo.ServiceName,
        inputParams,
        serviceInfo.Operation,
        null,
        null,
        OTP,
        serviceInfo.AprStat,
        serviceInfo.AprSeq,
        serviceInfo.MakerDt,
        serviceInfo.TimeOut
    );
    const reqInfo = new requestInfo(serviceInfo.reqFunct, inputParams, handleResultFunc, serviceInfo.WorkerName, serviceInfo.ServiceName)
    glb_sv.setReqInfoMapValue(clientSeq, reqInfo)

    // nu request khng control timeOut th warn cho developer bit
    if (!isControlTimeOut) console.warn('Khng control timeOut request [ServiceInfo]: ', serviceInfo, ' [inputParams]: ', inputParams);
    // // Nu  request v ang i phn hi th return
    const controlTimeOutKey = serviceInfo.WorkerName + '|' + serviceInfo.ServiceName + '|' + JSON.stringify(inputParams)
    // if (control_sv.ControlTimeOutObj[controlTimeOutKey]) {
    //     // console.warn('[sendRequest] Request ang c x l, vui lng i! Hm gi [reqFunct]: ', serviceInfo, ' [inputParams]: ', inputParams)
    //     return
    // }

    socket_sv.send2Sv(socket_sv.channels.REQ_MSG, JSON.stringify(inputServiceBody))
    if (isClearReqInfoMap === 'equal_input') {
        for (let i = 1; i < clientSeq; i++) {
            const reqInfoMap = glb_sv.getReqInfoMapValue(i);
            if (reqInfoMap && reqInfoMap.WorkerName === serviceInfo.WorkerName &&
                reqInfoMap.ServiceName === serviceInfo.ServiceName &&
                isEqual(reqInfoMap.inputParam) === isEqual(inputParams)) {
                glb_sv.setReqInfoMapValue(i, null);
            }
        }
    }
    if (isClearReqInfoMap === 'equal_service') {
        for (let i = 1; i < clientSeq; i++) {
            const reqInfoMap = glb_sv.getReqInfoMapValue(i);
            if (reqInfoMap && reqInfoMap.WorkerName === serviceInfo.WorkerName &&
                reqInfoMap.ServiceName === serviceInfo.ServiceName &&
                reqInfoMap.inputParam[0] === inputParams[0]) {
                glb_sv.setReqInfoMapValue(i, null);
            }
        }
    }
    // SetTimeout cho Request
    if (isControlTimeOut) control_sv.ControlTimeOutObj[controlTimeOutKey] = setTimeout(handleTimeOut, time || 10000, serviceInfo, controlTimeOutKey, onTimeout, reqInfo, clientSeq, inputParams);
}

const handleTimeOut = (serviceInfo, controlTimeOutKey, onTimeout, reqInfo, clientSeq, inputParams) => {
    console.log('Request b timeOut [reqFunct]: ', serviceInfo, ' [controlTimeOutKey]: ', controlTimeOutKey);
    control_sv.clearTimeOutRequest(controlTimeOutKey)
    // Clear lun handleResultFunc  trnh li khi server gi response v sau khi  timeout
    control_sv.clearReqInfoMapRequest(clientSeq)
    // X l time out cho tng mn hnh nu c
    onTimeout({ type: 'timeout', inputParams })
}

export default sendRequest
