function BackgroundServiceMemo() {
    const { t } = useTranslation()
    const { setConnected, loadingGlobal, setLoadingGlobal } = useContext(StoreContext)
    const { setAuth, setUserInfo } = useContext(StoreTrading)

    const reLoginCallTimeout = useRef(null)

    const flagListIndex = useRef(false)

    const reLoginFlag = useRef(false)
    const flagGetWatchlistOwner = useRef(0)
    const BackgroundServiceRealTime = new RealTimeHandler(Screens.BACKGROUND_SERVICE)

    useEffect(() => {
        // Lng nghe thay i v trng thi network v authen
        const eventConnect = glb_sv.eventConnect.subscribe((msg) => {
            if (msg.type === socket_sv.connect_event.REQ_RELOGIN) {
                if (reLoginCallTimeout.current) clearTimeout(reLoginCallTimeout.current)
                reLoginCallTimeout.current = setTimeout(() => {
                    reLogin()
                }, 100)
            }
        })
        // Lng nghe common Event

        if (socket_sv.stream_conect_first) {
            BackgroundServiceRealTime.subscribe({
                topic: ['EXCHANGE_TIME'],
                value: [''],
            })
            BackgroundServiceRealTime.subscribe({
                topic: ['MDDS|BI'],
                value: ['HSXIndex', 'LIS_BRD_01', 'UPC_BRD_01', 'LIS_BRD_BOND'],
            })
        }
        if (socket_sv.trading_conect_first) {
            if (!flagListIndex.current) getListIndex()
        }
        const commonEvent = glb_sv.commonEvent.subscribe((msg) => {
            if (msg.type === eventList.MESSAGE_SYSTEM) {
                const message = msg.data

                if (message['Code'] === 'XXXXX1') {
                    const ls_msg = 'Server_is_busy_process_Please_try_again_later'
                    // navigation.navigate(Screens.ALERT_MODAL, {
                    //     title: t('common_InfoMessage'),
                    //     content: t(ls_msg),
                    //     typeColor: styles.WARN__COLOR
                    // });
                    return
                } else if (message['Code'] === 'XXXXX2') {
                    // Message thng bo  ng nhp
                    // const messageEnt = {}
                    // messageEnt['type'] = glb_sv.misTypeReconect
                    // messageEnt['data'] = null
                    // glb_sv.commonEvent.next(messageEnt)
                    return
                } else if (message['Code'] === 'XXXXX4') {
                    // const ls_msg = 'You_are_not_authentication_OTP'
                    // glb_sv.openAlertModal('', 'common_InfoMessage', ls_msg, '', 'warning', '', '', message['Code'])
                } else if (
                    message['Code'] === 'XXXXX5' ||
                    message['Code'] === 'XXXX12' ||
                    message['Code'] === '080063'
                ) {
                    if (reLoginCallTimeout.current) clearTimeout(reLoginCallTimeout.current)
                    reLoginCallTimeout.current = setTimeout(() => {
                        reLogin()
                    }, 100)
                } else if (message['Code'] === 'XXXXX6') {
                    const ls_msg = 'common_login_expire_bc_newsession'
                    glb_sv.showAlert({
                        title: t('common_notify'),
                        content: t(ls_msg),
                        type: 'warn',
                        linkCallback: () => window.location.reload(),
                    })
                } else if (message['Code'] === 'XXXXX7') {
                    // glb_sv.authFlag = false
                    // const ls_msg = 'Your_session_time_out_System_will_disconnected_this_session_now'
                    // glb_sv.openAlertModal('', 'common_InfoMessage', ls_msg, '', 'warning', '', '', message['Code'])
                    // reauthen
                } else if (message['Code'] === 'XXXX10') {
                    // glb_sv.authFlag = false
                    // const ls_msg = 'Your_network_not_good_now_For_safly_reason_Please_try_to_login_again'
                    // glb_sv.openAlertModal('', 'common_InfoMessage', ls_msg, '', '', 'warning', '', message['Code'])
                    // reauthen
                } else if (message['Code'] === 'XXXX11') {
                    // Reauthen tht bi
                    // let incrTm
                    // if (glb_sv.disconTime != null) {
                    //     incrTm = new Date(glb_sv.disconTime.getTime() + glb_sv.discexpire * 60000) // -- tng 30'
                    // } else {
                    //     incrTm = new Date()
                    // }
                    // const curTime = new Date()
                    // const incrTmS = glb_sv.convDate2StrTime(incrTm)
                    // const curTimeS = glb_sv.convDate2StrTime(curTime)
                    // if (Number(curTimeS) > Number(incrTmS)) {
                    //     // glb_sv.authFlag = false
                    //     // this.setState({ cfm_try_connect_to_server: false })
                    //     // const ls_msg = 'Your_network_not_good_now_For_safly_reason_Please_try_to_login_again'
                    //     // glb_sv.openAlertModal('', 'common_InfoMessage', ls_msg, '', '', 'warning', '', message['Code'])
                    //     // disconnect socket -> connect socket -> reLogin
                    // } else {
                    //     Autologin()
                    // }
                }
            }
            // if (msg.type === eventList.RECONNECT_MARKET || msg.type === eventList.CONNECT_MARKET) {
            //     BackgroundServiceRealTime.subscribe({
            //         topic: ['EXCHANGE_TIME'],
            //         value: [''],
            //     })
            //     BackgroundServiceRealTime.subscribe({
            //         topic: ['MDDS|BI'],
            //         value: ['HSXIndex', 'LIS_BRD_01', 'UPC_BRD_01'],
            //     })
            // }
            if (msg.type === eventList.RES_COMMON_MSG) {
                if (msg.Code === '080063' || msg.Code == 'XXXXX5' || msg.Code === '010012') {
                    if (reLoginCallTimeout.current) clearTimeout(reLoginCallTimeout.current)
                    reLoginCallTimeout.current = setTimeout(() => {
                        reLogin()
                    }, 100)
                }
            }
            if (msg.type === eventList.CONNECT_TRADING) {
                if (!flagListIndex.current) getListIndex()
            }
            if (msg.type === eventList.MSG_ADD_NEW_STK_2FAV) {
                try {
                    const stk = msg.data.stk
                    const keyFlv = msg.data.keyFlv
                    if (!stk || !keyFlv) return
                    //-- x l thm stk vo danh mc c chn
                    if (glb_sv.authFlag) {
                        const InputParams = ['FAV_ITEM_ADD', keyFlv, stk]
                        sendRequest(ServiceInfo.AddStockToFavList, InputParams, handleAddStockToFavList, true, null)
                    } else {
                        let fvlArr,
                            fvlstr = localStorage.getItem('FAV_LIST_LS')
                        try {
                            fvlArr = JSON.parse(fvlstr)
                        } catch (error) {
                            return
                        }
                        if (!fvlArr || fvlArr.length === 0) return
                        const findIndex1 = fvlArr.findIndex((item) => item.c1 === keyFlv)
                        if (findIndex1 >= 0) {
                            const check = fvlArr[findIndex1]['ListStock'].some(x => x === stk);
                            if (!check) {
                                fvlArr[findIndex1]['ListStock'].unshift(stk);
                                localStorage.setItem('FAV_LIST_LS', JSON.stringify(fvlArr))
                                const findIndex = glb_sv.allListFav.findIndex(x => x.c1 === keyFlv)
                                glb_sv.allListFav[findIndex]['ListStock'].unshift(stk);
                                glb_sv.activeList = glb_sv.allListFav[findIndex];
                                glb_sv.currentIndex_FAV = findIndex;
                            }
                            glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE, value: null })
                        }
                    }
                } catch (error) {
                    return
                }
            }

            if (msg.type === eventList.NOTIFY_SERVER) {
                let jsondata;
                const temp = glb_sv.filterStrBfParse(msg.data.Data);
                jsondata = JSON.parse(temp);
                glb_sv.countNotifycation.push(jsondata)
                glb_sv.commonEvent.next({ type: 'COUNT_NOTIFI', data: glb_sv.countNotifycation })
            }
        })
        return () => {
            commonEvent.unsubscribe()
            eventConnect.unsubscribe()
        }
    }, [t])

    const handleAddStockToFavList = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            const findIndex = glb_sv.allListFav.findIndex((x) => x.c1 == reqInfoMap.inputParam[1])
            if (findIndex >= 0) {
                glb_sv.allListFav[findIndex].ListStock.unshift(reqInfoMap.inputParam[2])
                glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE, value: null })
            }
        }
    }

    const reLogin = () => {
        if (!glb_sv.isConnectApp) {
            if (reLoginCallTimeout.current) clearTimeout(reLoginCallTimeout.current)
            reLoginCallTimeout.current = setTimeout(() => {
                reLogin()
            }, 100)
            return
        }
        const username = glb_sv.credentials.username
        const password = glb_sv.credentials.password
        if (!username || !password) return
        const InputParams = [
            'login',
            username,
            password,
            glb_sv.notifyOnesignal.userId || '',
            glb_sv.notifyOnesignal.userId || '',
            'Y',
        ]
        const OTP = glb_sv.objShareGlb['sessionInfo']['Otp'] || 'NONE'
        sendRequest(ServiceInfo.LOGIN_FUNCTION, InputParams, handleReloginResult, true, handleTimeoutRelogin, OTP)
    }
    const handleReloginResult = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            if (message.Code === '080001' || message.Code === '010012') {
                socket_sv.anotherLoginFlag = true
                glb_sv.commonEvent.next({ type: eventList.MESSAGE_SYSTEM, data: { Code: '080001' } })
            }
        } else {
            console.warn('----------------------------- Relogin thnh cng')
            socket_sv.establishSocketConnect('authen_success', {})
            setConnected(true)
            glb_sv.isConnectApp = true

            let dataArr = {}
            try {
                let strdata = message['Data']
                dataArr = JSON.parse(strdata)[0]
            } catch (error) {
                return
            }
            glb_sv.objShareGlb['userInfo'] = dataArr
            glb_sv.objShareGlb.otpType = Number(dataArr.c6)

            glb_sv.objShareGlb['sessionInfo'] = {
                sessionId: dataArr.c0,
                userID: dataArr.c1,
                passID: reqInfoMap.inputParam[2],
                remOrdPass: false,
                orderPass: null,
                Otp: '',
            }

            const dataLocal = localStorage.getItem(STORE_KEY.USER_INFO)
            if (dataLocal) {
                const data = JSON.parse(dataLocal)
                if (data && data.email === reqInfoMap.inputParam[1]) {
                    glb_sv.userInfo = { ...data }
                }
            }

            setTimeout(() => {
                glb_sv.sendAuthReqSocketStream({
                    LoginID: dataArr.c1,
                    MdmTp: '02',
                    Token: dataArr.c0,
                })
            }, 2000)

            storeAccountInfo(dataArr)

            reLoginFlag.current = true
            flagGetWatchlistOwner.current = 0
            getFavInfo()
            // getStockListOwn();

            glb_sv.commonEvent.next({ type: eventList.LOGIN_SUCCESS })
        }
    }

    const handleTimeoutRelogin = () => {
        if (reLoginCallTimeout.current) clearTimeout(reLoginCallTimeout.current)
        reLoginCallTimeout.current = setTimeout(() => {
            reLogin()
        }, 100)
    }

    const storeAccountInfo = (userInfo) => {
        const accountInfo = userInfo.c12
        // const accountInfo = '888C000351,00,888C000351,1,Y,888|888C000351,01,888C000351,2,Y,888|888C000351,02,888C000351,2,Y,888|888C000351,03,888C000351,2,Y,888'
        if (!accountInfo) {
            setAuth(true)
            glb_sv.authFlag = true
            return
        }
        const array_acnt = accountInfo.split('|')
        const js_acntInfo = []
        const js_acntList = []
        // const js_acntListAll = []
        let acntMain = '',
            acntMainName = ''
        for (let i = 0; i < array_acnt.length; i++) {
            const acntInfo = array_acnt[i]
            const arr_info = acntInfo.split(',')
            // if (i === 0) {
            //     const ls_acntObjsAll = {
            //         id: arr_info[0] + '.%',
            //         name: arr_info[0] + '.% - ' + arr_info[2],
            //     }
            //     js_acntListAll.push(ls_acntObjsAll)
            // }
            if (arr_info[4] === 'Y' && arr_info[1] === '00') {
                acntMain = arr_info[0]
                acntMainName = arr_info[2]
            }
            const js_acnt = {
                AcntNo: arr_info[0],
                SubNo: arr_info[1],
                AcntNm: arr_info[2],
                IsOwnAcnt: arr_info[4],
                MarginYN: arr_info[3] === '2',
                ActType: arr_info[3],
                // Broker: arr_info[20],
                // BrokerBranch: arr_info[21],
                // BrokerAgent: arr_info[22],
                // TradingFee: arr_info[23],
            }
            const js_acntObj = {
                value: arr_info[0] + '.' + arr_info[1],
                label: arr_info[0] + '.' + arr_info[1] + ' - ' + arr_info[2],
            }
            js_acntInfo.push(js_acnt)
            js_acntList.push(js_acntObj)
            // js_acntListAll.push(js_acntObj)
        }

        glb_sv.objShareGlb['acntNoInfo'] = js_acntInfo
        glb_sv.objShareGlb['acntNoList'] = js_acntList
        // glb_sv.objShareGlb['acntNoListAll'] = js_acntListAll
        // --------------------
        glb_sv.objShareGlb['AcntMain'] = acntMain // -- ch ti khon lu k
        glb_sv.objShareGlb['AcntMainNm'] = acntMainName // -- Tn ch ti khon
        glb_sv.objShareGlb['workDate'] = userInfo.c16 // -- Ngy lm vic
        // glb_sv.objShareGlb['brokerId'] = userInfo['c24'] // -- ID nhn vin mi gii
        // glb_sv.objShareGlb['brokerNm'] = userInfo['c25'] // -- Tn nhn vin mi gii
        // glb_sv.objShareGlb['brokerEmail'] = userInfo['c26'] // -- Email nhn vin mi gii
        // glb_sv.objShareGlb['brokerMobile'] = userInfo['c27'] // -- Mobile nhn vin mi gii
        // glb_sv.objShareGlb['secCode'] = userInfo['c9'] // -- Tn nhn vin mi gii
        // glb_sv.objShareGlb['prdYN'] = userInfo['c31'] // -- User trnh chiu bng in hay khng
        glb_sv.objShareGlb['verify'] = Number(userInfo['c37']) || 0 // -- verify > 0 => Phi xc thc chng ch s
        glb_sv.objShareGlb['serialnum'] = userInfo['c38'] // -- serialNumber

        const sub_list = []
        const actn_list = []
        js_acntInfo.forEach((e) => {
            if (e.AcntNo === acntMain) sub_list.push(e.SubNo)
            if (!actn_list.find((temp) => temp.AcntNo === e.AcntNo))
                actn_list.push({
                    AcntNo: e.AcntNo,
                    actn_name: e.AcntNm,
                    key: e.AcntNo,
                    label: e.AcntNo + ' (' + e.AcntNm + ')',
                })
        })
        const isMargin = glb_sv.objShareGlb['acntNoInfo'].find((e) => e.AcntNo === acntMain && e.SubNo === sub_list[0])
            ?.MarginYN
        setUserInfo({
            actn_curr: acntMain,
            sub_curr: sub_list[0],
            actn_name: acntMainName,
            sub_list,
            // sub_list: ['00','01','02','03','04','05','06','07','08'],
            actn_list,
            actn_full: acntMain + '.00',
            isMargin: isMargin || false,
        })
        setAuth(true)
        glb_sv.authFlag = true
    }

    const getFavInfo = () => {
        const InputParams = ['3', '', '0']
        sendRequest(ServiceInfo.getFav, InputParams, getFavInfoProc)
    }

    const getFavInfoProc = (reqInfoMap, message) => {
        let jsonArr = []
        const strdata = message['Data']
        try {
            jsonArr = strdata ? JSON.parse(strdata) : []
        } catch (error) {
            console.log(error)
            return
        }
        if (Number(message['Result']) === 1 && jsonArr.length > 0) {
            try {
                const newallListFav = []
                for (let i = 0; i < jsonArr.length; i++) {
                    const element = { key: jsonArr[i]['c1'], name: jsonArr[i]['c2'] }
                    newallListFav.push({
                        ...element,
                        ListStock: [],
                    })
                }
                glb_sv.allListFav = newallListFav
                glb_sv.activeList = newallListFav[0] ? { ...newallListFav[0] } : { ListStock: [] }
                jsonArr.forEach((e) => getDetailFavList(e))
            } catch (error) { }
        }
    }

    const getDetailFavList = (element) => {
        const groupId = element.c1
        const InputParams = ['4', String(groupId)]
        sendRequest(ServiceInfo.getDetailList, InputParams, handleGetDetailFavList)
    }

    const handleGetDetailFavList = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
            return
        } else {
            reqInfoMap.procStat = 1
            let dataArr = []
            try {
                let strdata = message['Data']
                dataArr = JSON.parse(strdata)
                const findIndex = glb_sv.allListFav.findIndex((x) => x.c1 == reqInfoMap.inputParam[1])
                if (findIndex >= 0) {
                    glb_sv.allListFav[findIndex].ListStock = dataArr.map((e) => e.c3)
                    glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
                }
                // if (glb_sv.allListFav.length) {
                //     const obj = glb_sv.allListFav[0]
                //     if (obj.c1 === reqInfoMap.inputParam[1]) {
                //         obj.ListStock = dataArr.map((e) => e.c3)
                //         glb_sv.activeList = { ...obj }
                //         glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
                //     }
                // }
            } catch (error) {
                return
            }
        }
    }

    const getListIndex = () => {
        const InputParams = ['idx_list', '%']
        sendRequest(ServiceInfo.GET_INDEX_LIST, InputParams, hanldeGetListIndex, false)
    }

    const hanldeGetListIndex = (reqInfoMap, message) => {
        // -- process after get result --
        if (Number(message['Result']) === 0) {
            return
        } else {
            let jsondata
            try {
                jsondata = message['Data'] ? JSON.parse(message['Data']) : []
                console.log(jsondata)
            } catch (err) {
                console.log('hanldeGetListIndex -> err', err)
                jsondata = []
            }
            // flagListIndex.current = true
            jsondata.forEach((item) => {
                if (glb_sv.IndexMarket[item.c1]) {
                    glb_sv.IndexMarket[item.c1].indexName = item.c3
                } else {
                    glb_sv.IndexMarket[item.c1] = new InfoIndex()
                    glb_sv.IndexMarket[item.c1].indexName = item.c3
                }
            })
            glb_sv.jsonIndexInfo = jsondata
            // sort li data server tr v
            const listIndexSorted = glb_sv.listIndex.config
            jsondata.map((idxInfo) => {
                if (!listIndexSorted.includes(idxInfo.c1)) {
                    listIndexSorted.push(idxInfo.c1)
                }
            })
            glb_sv.listIndex.server = listIndexSorted //  jsondata.map(e => e.c1); // !!! {Tuan} Em sa li ch ny xu
            // Lu li info theo key l index
            jsondata.map((item) => {
                glb_sv.listIndex.InfoIndexServerByKey[item.c1] = item
            })

            glb_sv.eventMarket.next({ type: eventList.GET_LIST_INDEX })
            glb_sv.eventMarket.next({ type: eventList.SUB_INDEX })

            jsondata.forEach((e) => {
                const dextObj = {}
                let ls_sanGd = 'HNX'
                if (e.c1 === 'HSXIndex' || e.c2 === 'HSXIndex') {
                    ls_sanGd = 'HOSE'
                }
                const symbol = e.c1
                dextObj['symbol'] = symbol
                dextObj['full_name'] = symbol
                dextObj['description'] = e.c3
                dextObj['exchange'] = ls_sanGd
                dextObj['type'] = 'index'
                if (
                    glb_sv.tradview_StkList.length === 0 ||
                    glb_sv.tradview_StkList.findIndex((x) => x.symbol === symbol) < 0
                ) {
                    glb_sv.tradview_StkList.push(dextObj)
                    const stkTradInfo = {}
                    stkTradInfo['name'] = symbol
                    stkTradInfo['full_name'] = symbol
                    stkTradInfo['exchange'] = ls_sanGd
                    stkTradInfo['exchange-traded'] = ls_sanGd
                    stkTradInfo['exchange-listed'] = ls_sanGd
                    stkTradInfo['timezone'] = 'Asia/Bangkok'
                    stkTradInfo['minmov'] = 1
                    stkTradInfo['minmov2'] = 0
                    stkTradInfo['pointvalue'] = 1
                    stkTradInfo['session'] = '0915-1530'
                    stkTradInfo['has_intraday'] = true
                    stkTradInfo['has_seconds'] = true
                    // stkTradInfo['has_daily'] = true;
                    stkTradInfo['intraday_multipliers'] = ['1S', '5S', '15S', '30S', '1', '5', '15', '30', '60']
                    // stkTradInfo['intraday_multipliers'] = ['1', '60'];
                    // stkTradInfo['seconds_multipliers'] = ['1', '5', '15', '30'];
                    stkTradInfo['has_no_volume'] = false
                    stkTradInfo['pricescale'] = 100
                    stkTradInfo['description'] = e.c3
                    stkTradInfo['type'] = 'index'
                    stkTradInfo['currency_code'] = 'VND'
                    stkTradInfo['supported_resolutions'] = [
                        '1S',
                        '5S',
                        '15S',
                        '30S',
                        '1',
                        '5',
                        '15',
                        '30',
                        '60',
                        'D',
                        'W',
                        'M',
                        '3M',
                    ]
                    // stkTradInfo['supported_resolutions'] = ['1', '5', '15', '30', '60', 'D', 'W', 'M', '3M'];
                    stkTradInfo['ticker'] = symbol
                    glb_sv.stkInfoTradviewMap.set(symbol, stkTradInfo)
                    if (glb_sv.tradingViewFlag) {
                        const mapArr = []
                        glb_sv.stkInfoTradviewMap.forEach((value, key) => {
                            const mapObj = {}
                            mapObj[0] = key
                            mapObj[1] = value
                            mapArr.push(mapObj)
                        })
                        sessionStorage.setItem('tradinglist', JSON.stringify(glb_sv.tradview_StkList))
                        sessionStorage.setItem('tradingmap', JSON.stringify(mapArr))
                    }
                }
            })
            glb_sv.eventMarket.next({ type: eventList.GET_INDEXS_DONE })
        }
    }

    const getStockListOwn = () => {
        if (!glb_sv.objShareGlb['AcntMain']) return
        const InputParams = [glb_sv.objShareGlb['AcntMain']]
        sendRequest(
            ServiceInfo.GET_LIST_OWN,
            InputParams,
            getStockListOwnResult,
            true,
            getStockListOwnTimeout,
            '',
            0,
            'equal_input'
        )
    }

    const getStockListOwnTimeout = () => null

    const getStockListOwnResult = (reqInfoMap, message) => {
        if (Number(message['Result']) === 0) {
        } else {
            let jsondata = []
            try {
                jsondata = message.Data ? JSON.parse(message.Data) : []
            } catch (err) {
                return
            }
            const newWatchlist = {
                c1: 'watchlist_owner',
                c2: 'own_stock_list',
                type: 'own',
                stkList: jsondata.map((e) => e.c0),
            }
            glb_sv.allListFav.push(newWatchlist)
            flagGetWatchlistOwner.current += 1
            if (reLoginFlag.current) {
                if (flagGetWatchlistOwner.current === 2) {
                    if (glb_sv.activeList && glb_sv.allListFav.find((e) => e.c1 === glb_sv.activeList.c1)) {
                        glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
                    } else {
                        glb_sv.activeList = glb_sv.allListFav[0] ? { ...glb_sv.allListFav[0] } : { ListStock: [] }
                        glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
                    }
                }
            } else {
                glb_sv.activeList = newWatchlist
                glb_sv.commonEvent.next({ type: eventList.CHANGE_LIST_ACTIVE })
            }
        }
    }

    return (
        <>
            <Modal
                visible={loadingGlobal.status}
                transparent
                wrapClassName='modal-loading'
            >
                <div style={{
                    height: dimensions.vertical(56),
                    backgroundColor: 'var(--BG__1)'
                }}>
                    <span className="center__item" style={{
                        height: '100%',
                    }}>
                        <svg className="loader" width="32" height="32" viewBox="0 0 32 32" fill="#24253D" xmlns="http://www.w3.org/2000/svg">
                            <path d="M31 15.5C31 13.1155 30.4119 10.7677 29.2879 8.66475C28.1638 6.56177 26.5384 4.76847 24.5558 3.44369C22.5731 2.11891 20.2943 1.30355 17.9212 1.06982C15.5482 0.836096 13.1541 1.19122 10.9511 2.10375C8.74806 3.01627 6.80408 4.45802 5.29135 6.3013C3.77861 8.14457 2.74381 10.3325 2.27861 12.6712C1.81341 15.0099 1.93217 17.4273 2.62436 19.7091C3.31656 21.991 4.56083 24.0669 6.24695 25.753L7.60845 24.3916C6.14622 22.9293 5.06718 21.1291 4.4669 19.1502C3.86662 17.1713 3.76363 15.075 4.16706 13.0468C4.57049 11.0187 5.46788 9.12129 6.77974 7.52279C8.0916 5.92428 9.77744 4.67397 11.6879 3.88262C13.5984 3.09127 15.6746 2.7833 17.7325 2.98599C19.7905 3.18868 21.7667 3.89578 23.486 5.04464C25.2054 6.1935 26.615 7.74867 27.5898 9.5724C28.5646 11.3961 29.0746 13.4321 29.0746 15.5H31Z" fill="#24253D" />
                        </svg>
                        <span className="fs__medium" onClick={() => setLoadingGlobal({ status: false, message: '' })}>&nbsp;&nbsp;&nbsp;{loadingGlobal.message}</span>
                    </span>
                </div>
            </Modal>
            {/* <EkycModal /> */}
            <AlertModal />
            <OtpModal />
        </>
    )
}
const BackgroundService = memo(BackgroundServiceMemo)
export default BackgroundService
